@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,800;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap);

@-webkit-keyframes enter {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0.2;
  }
  75% {
    opacity: 0.75;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@keyframes enter {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0.2;
  }
  75% {
    opacity: 0.75;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes modal {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes modal {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes square1 {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@keyframes square1 {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@-webkit-keyframes square2 {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}
@keyframes square2 {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}
@-webkit-keyframes square3 {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@keyframes square3 {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@-webkit-keyframes square4 {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@keyframes square4 {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  scroll-behavior: smooth;
  overflow: hidden;
}

body {
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "poppins", "roboto", sans-serif;
}

#root {
  height: 100%;
}

img {
  max-width: 100%;
  display: block;
}

.home,
.livestream,
.logout {
  display: flex;
  height: 90vh;
  font-size: 2rem;
}

.app {
  display: grid;
  height: 100%;
  grid-template-columns: 20% 80%;
}

.home {
  display: block;
  padding: 2rem 4rem;
}
.home__display {
  border-radius: 6px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  padding: 2rem 10rem;
  background: white;
  color: #d9843b;
  margin: 40px;
}
.home__display--item {
  text-align: center;
}
.home__display--item p {
  font-size: 1.3rem;
}
.home__display--item h {
  font-size: 4rem;
  font-weight: bold;
}

.Sidebar {
  background-color: #ff6b00;
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.username {
  display: flex;
  margin-right: 1rem;
  order: 2;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  order: 1;
}
.menu-bars svg {
  color: white;
}

.nav-menu {
  background-color: #ff6b00;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 2;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #994000;
}

.nav-menu-items {
  width: 100%;
  background-color: #ff6b00;
}

.navbar-toggle {
  background-color: #ff6b00;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.sidebar_item_title {
  margin-left: 16px;
}

.main {
  grid-column: 2/3;
  grid-row: 1/2;
  height: 100%;
}

.settings {
  padding: 20px;
  height: 100%;
}

.sub_Header {
  font-size: 1.2rem;
  font-weight: bold;
}

.wrapper {
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  height: 100%;
}

.w1, .w2, .w3 {
  border-radius: 6px;
}

.w1 {
  background-color: white;
  display: grid;
  grid-column: 1;
  grid-row: 1;
  padding: 2rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.w1 .profilePic {
  display: flex;
  align-items: center;
}
.w1 .profilePic h4 {
  padding-left: 20px;
}
.w1 .uname {
  grid-column: 1;
  grid-row: 2;
}
.w1 .email {
  grid-column: 2;
  grid-row: 2;
}

.w2 {
  background-color: white;
  padding: 2rem;
  display: grid;
  grid-column: 2;
  grid-row: 1;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.w2 .password .passRadius {
  display: inline-block;
  padding: 15px 20px;
  border: none;
  cursor: pointer;
}
.w2 .password .passRadius:hover {
  background-color: #D9843B;
}

.w3 {
  background-color: white;
  padding: 2rem;
  display: grid;
  grid-column: 2;
  grid-row: 2;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.w3 .accountClose .passRadius {
  display: inline-block;
  padding: 15px 20px;
  border: none;
  cursor: pointer;
}
.w3 .accountClose .passRadius:hover {
  background-color: #D9843B;
}

.login-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.login-container .logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-container .logo-container .logo-text {
  font-size: 3em;
  font-family: "Orbitron";
  color: #ff6b00;
}
.login-container .login-form {
  height: 70%;
  width: 50%;
  display: flex;
  flex-direction: column;
}
.login-container .login-form > * {
  margin: 0rem 3rem;
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.login-container .login-title {
  font-size: 1.5em;
  font-weight: 600;
}
.login-container .label,
.login-container .field {
  display: block;
}
.login-container .field,
.login-container .login-btn {
  font-size: 1.5em;
  border-radius: 5px;
  padding: 0.5rem;
}
.login-container .field:focus {
  border: 2px solid blue;
}
.login-container .login-btn {
  background-color: #0075ff;
  color: white;
  border: none;
  cursor: pointer;
}
.login-container .login-btn:hover {
  background-color: #0075eb;
}

.upload {
  padding: 20px;
  height: 100%;
}

.Up {
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  height: 30%;
}

h2 {
  display: block;
  font-size: 1.5em;
  -webkit-margin-before: 0.83em;
          margin-block-start: 0.83em;
  -webkit-margin-after: 0.83em;
          margin-block-end: 0.83em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

.select {
  border-radius: 8px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  justify-content: space-between;
  padding: 1rem;
  background: white;
  color: #d9843b;
  margin: 20px;
}
.select .selectUp {
  border: none;
  display: inline-block;
  padding: 20px 20px;
  border: none;
  cursor: pointer;
}
.select .selectUp:hover {
  background-color: #D9843B;
}

.gallery__items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 2rem;
  grid-gap: 20px;
  gap: 20px;
}
.gallery__items--item {
  background: #d9843b;
}
.gallery__items--item img {
  width: 100%;
}
.gallery__items .desc {
  padding: 20px;
  text-align: center;
}
.gallery__items h1 {
  margin: 1rem;
  padding: 20px;
  text-align: center;
}

.explorer {
  height: 90%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr;
}
.explorer .explorer_tree {
  grid-column: 1/4;
  list-style-type: none;
  border-right: 1px solid #d4d4d4;
  overflow: auto;
}
.explorer .explorer_tree .folderName {
  font-size: 1.25em;
  margin-left: 1em;
  display: flex;
  align-items: center;
}
.explorer .explorer_tree .folderName .folderName-text {
  margin-left: 1rem;
}
.explorer .explorer_tree .subtree {
  list-style-type: none;
}
.explorer .view-grid-outer {
  grid-column: 4/13;
  display: flex;
  flex-direction: column;
}
.explorer .view-grid-outer .cd-info {
  max-height: 20%;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  border-bottom: 1px solid #d4d4d4;
}
.explorer .view-grid-outer .cd-info > * {
  margin: 1em 1em;
}
.explorer .view-grid-outer .cd-info .pathCrumb:nth-child(odd) {
  height: 100%;
  cursor: pointer;
}
.explorer .view-grid-outer .cd-info .pathCrumb:hover {
  text-decoration: underline;
}
.explorer .view-grid-outer .view-grid-inner {
  max-height: 80%;
  width: 100%;
  justify-self: center;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  top: 0;
  bottom: 0;
  z-index: 0;
  overflow: auto;
}
.explorer .view-grid-outer .fileItem {
  font-size: 1.25em;
  width: 10%;
  margin: 1rem;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.explorer .view-grid-outer .fileItem:hover {
  background-color: silver;
  cursor: pointer;
}
.explorer .view-grid-outer .fileItem .filename {
  display: block;
  line-break: anywhere;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
  text-align: center;
}
.explorer .view-grid-outer .fileItem .fileIcon {
  font-size: 2.5em;
  color: #ff6b00;
}
@media (max-width: 37.5em) {
  .explorer .view-grid-outer {
    grid-column: 1/13;
  }
  .explorer .explorer_tree {
    display: none;
  }
}

.sublist {
  cursor: pointer;
  margin: 1rem;
}
.sublist .subfolder {
  display: flex;
}
.sublist .subfolder:hover {
  background-color: #ff6b00;
  color: white;
}
.sublist .subtree-container {
  margin-left: 1rem;
}

.fileInfo-container {
  background-color: rgba(0, 0, 0, 0.212);
  height: 90%;
  width: 100%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 10%;
  bottom: 0;
  z-index: 1;
}
.fileInfo-container .fileInfo {
  background-color: white;
  border-radius: 5px;
  width: 50%;
  max-height: 90%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.fileInfo-container .fileInfo table {
  margin: 1em;
}
.fileInfo-container .fileInfo th,
.fileInfo-container .fileInfo td {
  padding: 1em;
  text-align: left;
}
.fileInfo-container .fileInfo .download {
  margin: 1em;
  width: 50%;
  align-self: center;
  padding: 1em;
  border: none;
  border-radius: 5px;
  font-size: 1.25em;
  background-color: #0075ff;
  color: white;
  border: none;
  cursor: pointer;
}
.fileInfo-container .fileInfo .download:hover {
  background-color: #0075eb;
}
.fileInfo-container .fileInfo .close-modal {
  background-color: red;
  color: white;
  border: none;
  font-size: 1.5em;
  width: 10%;
  align-self: flex-end;
}

